$font_path: '../fonts';

@font-face {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 300;
	src: url('#{$font_path}/montserrat-v12-latin-300.eot');
	src: local("Montserrat Light"),
	 local("Montserrat-Light"), 
	 url('#{$font_path}/montserrat-v12-latin-300.eot?#iefix') format("embedded-opentype"), 
	 url('#{$font_path}/montserrat-v12-latin-300.woff2') format("woff2"), 
	 url('#{$font_path}/montserrat-v12-latin-300.woff') format("woff"), 
	 url('#{$font_path}/montserrat-v12-latin-300.ttf') format("truetype"), 
	 url('#{$font_path}/montserrat-v12-latin-300.svg#Montserrat') format("svg");
}

